import * as React from "react";
import * as PropTypes from "prop-types";
import {
  IconClose,
  IconError_outline,
  IconInfo_outline,
  IconSuccess_outline,
  IconWarning_outline
} from "sq-web-icons";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface NotificationProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
  /**
   * Callback on close
   */
  onClose?: (event: React.SyntheticEvent<HTMLElement>) => void;

  /**
   * Level of the alert, this is mandatory
   */
  level: "info" | "warning" | "error" | "success";
}

export default class Notification extends NamespaceWrapper<NotificationProps, {}> {
  static displayName = "Alert";

  public static propTypes = {//
    "level": PropTypes.oneOf(["info", "warning", "error", "success"]).isRequired, //
    "className": PropTypes.string, //
    "onClose": PropTypes.func
  };

  getIcon() {
    switch (this.props.level) {
      case "info":
        return <IconInfo_outline />;
      case "error":
        return <IconError_outline />;
      case "success":
        return <IconSuccess_outline />;
      case "warning":
        return <IconWarning_outline />;
    }

    return <IconInfo_outline />;
  }

  public render() {
    const {namespace, className, level, onClose, children, ...props} = this.props;
    return <div className={this.classes("Notification", "Notification--" + level, className)} {...props}>
      <div className={this.classes("Notification__container")}>
        <div className={this.classes("Notification__icon")}>{this.getIcon()}</div>
        <div className={this.classes("Notification__content")}>
          {onClose == null ? null : <button className={this.classes("Notification__close")} onClick={onClose}><IconClose /></button>}
          {children}
        </div>
      </div>
    </div>;
  }
}

export interface NotificationActionsProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
}

export class NotificationActions extends NamespaceWrapper<NotificationActionsProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <div className={this.classes("Notification__actions", className)} {...props} />;
  }
}
